import axios from "axios";

const logoutService = axios.create({
    baseURL:  process.env.VUE_APP_LARAVEL_BASE_URL_API + '/logout',
    withCredentials: true,
    headers: {
        "Content-type": "application/json",
        "Accept": "application/json",
    }
})

// use interceptor for push token into header
logoutService.interceptors.request.use( config => {
    let token
    try {
        let userStorage = localStorage.getItem('danphranakorn_user')
        let userStorageJSON = JSON.parse(userStorage);
        token = userStorageJSON['token']
    } catch (error) {
        console.log(error);
    }

    if(token){
        config.headers.Authorization = `Bearer ${token}`
    }

    return config
})

export default logoutService;