<template>
    <div class="fixed top-0 left-0 z-20 flex items-center justify-center w-full h-full modal">
        <div class="absolute w-full h-full bg-gray-900 opacity-70 modal-overlay"></div>
        <div class="z-50 w-11/12 p-5 mx-auto overflow-y-auto bg-white rounded shadow-lg h-4/5 modal-container md:max-w-md">
            <!-- Header -->
            <div class="flex items-center justify-center w-full h-auto">
                <div class="flex items-start justify-start w-full h-auto py-2 text-xl font-bold text-green-600">
                    เพิ่มสินค้าใหม่
                </div>
                <!-- exit modal button -->
                <div @click="closeNewProductModal()" class="flex justify-center w-1/12 h-auto cursor-pointer">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-x"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
                </div>
            <!--Header End-->
            </div>
            <!-- Modal Content-->
            <div class="w-full h-auto mb-4">
                <!-- 
                    @submit.prevent use for when click submit button page will not reload
                    enctype="multipart/form-data" use for sent files(image, gif,...)
                -->
                <form enctype="multipart/form-data" @submit.prevent="onNewProductSubmit"> 
                    <!-- product name -->
                    <label class="block my-3 text-gray-700 text-md" for="name">ชื่อสินค้า</label>
                    <input v-model="name" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="Product name">
                    <div v-if="v$.name.$error" class="mt-2 text-sm text-red-500">
                        {{ v$.name.$errors[0].$message }}
                    </div>
                    <!-- slug -->
                    <label class="block my-3 text-gray-700 text-md" for="slug">สลัก</label>
                    <input  v-model="slug" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="product-name">
                    <div v-if="v$.slug.$error" class="mt-2 text-sm text-red-500">
                        {{ v$.slug.$errors[0].$message }}
                    </div>                                    
                    <label class="block my-3 text-gray-700 text-md" for="description">รายละเอียด</label>
                    <textarea
                        class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow"
                        rows="5"
                        placeholder="Product description"
                        v-model="description"
                    ></textarea>

                    <label class="block my-3 text-gray-700 text-md" for="price">ราคา</label>
                    <input  v-model="price" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="0.00">
                    <div v-if="v$.price.$error" class="mt-2 text-sm text-red-500">
                        {{ v$.price.$errors[0].$message }}
                    </div>                                    
                    <div class="mt-4">
                        <img v-if="imgUrl" :src="imgUrl" class="w-full" />
                    </div>

                    <label class="block my-3 text-gray-700 text-md" for="image">ภาพสินค้า</label>
                    <input ref="fileUpload" @change="onFileChange" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="file">
        
                    <div class="grid grid-cols-3 gap-4">
                        
                        <div class="col-span-2">
                            <button class="w-full px-4 py-2 mt-4 font-medium leading-5 text-white transition-colors duration-150 bg-blue-600 border border-transparent rounded-lg text-md active:bg-purple-600 hover:bg-blue-700 focus:outline-none focus:shadow-outline-purple">
                                บันทึกรายการ
                            </button>
                        </div>

                        <div>
                            <button @click="resetForm()" class="w-full px-4 py-2 mt-4 font-medium leading-5 text-white transition-colors duration-150 bg-gray-500 border border-transparent rounded-lg text-md active:bg-purple-600 hover:bg-gray-700 focus:outline-none focus:shadow-outline-purple">
                                ล้าง
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
/** vuelidate */
import useVuelidate from '@vuelidate/core'
import { required, decimal } from '@vuelidate/validators'

/** axios */
import http from '@/services/ProductService.js'

export default {
    name: "EditProductModal",
    setup () {
        return { v$: useVuelidate() }
    },
    data() {
        return {
            name: '',
            description: '',
            slug: '',
            price: '',
            imgUrl: '',
            file: null,
        }
    },
    methods: {
        closeNewProductModal(){
            this.$emit("closeNewProductModal");
        },

        refreshProductTable(){
            this.$emit("refreshProductTable")
        },

        onFileChange(e){
            const file = e.target.files[0]
            this.file = e.target.files[0]
            this.imgUrl = URL.createObjectURL(file)
        },

        
        resetForm(){
            // newProductModal
            this.$refs.fileUpload.value = null
            this.name = ''
            this.description= ''
            this.slug = ''
            this.price = ''
            this.imgUrl = ''
            this.file = ''
        },

        async onNewProductSubmit(){
            // sweet alert 2 setup
            const Toast = this.$swal.mixin({
              toast: true,
              position: 'top-end',
              showConfirmButton: false,
              timer: 2500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
              }
            });

            // Validate (Vuelidate)
            const result = await this.v$.$validate()
            if (!result) {
                return
            }

            let newProduct = new FormData()
            newProduct.append('name', this.name)
            newProduct.append('slug', this.slug)
            newProduct.append('description', this.description)
            newProduct.append('price', this.price)
            newProduct.append('file', this.file)
            
            http.post('/',
                newProduct
            ).then( response =>{
                Toast.fire({
                icon: 'success',
                title: 'Created new product'
                }).then(()=>{
                    console.log(response);
                    this.resetForm()
                    this.refreshProductTable()
                    this.closeNewProductModal()
                })
            })
        },

    },

    validations () {
        return {
            name: { required },
            slug: { required },
            price: {
                required,
                decimal
            },
        }
    }
}
</script>