<template>
    <div class="px-10">
        <Carousel :settings='settings' :autoplay="2500">
            <Slide v-for="slide in 10" :key="slide">
                <div class="bg-white bg-center bg-cover carousel__item" 
                     :style="{ backgroundImage: 'url(' + require('@/assets/img/carousel/' + slide + '.png') + ')' }">
                  <!-- {{ slide }} -->
                </div>
            </Slide>

            <template #addons>
                <Pagination class="opacity-25"/>
            </template>
        </Carousel>
    </div>
  
</template>

<script>

import 'vue3-carousel/dist/carousel.css';
import { Carousel, Pagination, Slide } from 'vue3-carousel';

export default {
  name: "About",
  components: {
    Carousel,
    Slide,
    Pagination
  },
  data: () => ({
    // carousel settings
    settings: {
      itemsToShow: 1,
      snapAlign: 'center',
      pauseAutoplayOnHover: true,
      wrapAround: true
    },
    // breakpoints are mobile first
    // any settings not specified will fallback to the carousel settings
  }),
};
</script>

<style scoped>
.carousel__item {
  min-height: 250px;
  width: 100%;
  background-color: var(--carousel-color-primary);
  color:  var(--carousel-color-white);
  font-size: 20px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel__slide {
  padding: 20px;
}

.carousel__slide > .carousel__item {
  transform: scale(0.9);
  opacity: 0.5;
  transition: 0.5s;
}
.carousel__slide--visible > .carousel__item {
  opacity: 1;
  transform: rotateY(0);
}
.carousel__slide--next > .carousel__item {
  transform: scale(0.9) translate(-10px);
}
.carousel__slide--prev > .carousel__item {
  transform: scale(0.9) translate(10px);
}
.carousel__slide--active > .carousel__item {
  transform: scale(1.1);
}

</style>