import { createStore } from 'vuex'

export default createStore({
  state: {
    isLoggedin: false,
    avatar: null
  },
  mutations: {
    checkLoggedin(state){
      if(localStorage.getItem('danphranakorn_user')){
         state.isLoggedin = true
         let userStorage = localStorage.getItem('danphranakorn_user')
         let userStorageJSON = JSON.parse(userStorage);
         state.avatar = userStorageJSON['user'].avatar
      }else{
        state.isLoggedin = false
        state.avatar = null
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
