<template>
    <!-- Popup สำหรับการแก้ไขสินค้า -->
    <div id="editProductModal" class="fixed top-0 left-0 z-20 flex items-center justify-center w-full h-full modal">
        <div class="absolute w-full h-full bg-gray-900 opacity-70 modal-overlay"></div>

        <div class="z-50 w-11/12 p-5 mx-auto overflow-y-auto bg-white rounded shadow-lg h-5/6 modal-container md:max-w-md">
            <!-- Header -->
            <div class="flex items-center justify-center w-full h-auto">
                <div class="flex items-start justify-start w-full h-auto py-2 text-xl font-bold text-yellow-600">
                    แก้ไขสินค้า ( ID: {{edit_productId}} )
                </div>
                <div @click="closeEditProductModal()" class="flex justify-center w-1/12 h-auto cursor-pointer">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-x"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
                </div>
            <!--Header End-->
            </div>
            <!-- Modal Content-->
            <div class="w-full h-auto mb-4">

                <form  @submit.prevent="onSubmit" enctype="multipart/form-data">
                    
                    <label class="block my-3 text-gray-700 text-md" for="ename">ชื่อสินค้า</label>
                    <input v-model="edit_name" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="Product name">

                    <label class="block my-3 text-gray-700 text-md" for="eslug">สลัก</label>
                    <input v-model="edit_slug" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="product-name">

                    <label class="block my-3 text-gray-700 text-md" for="edescription">รายละเอียด</label>
                    <textarea
                        v-model="edit_description"
                        class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow"
                        rows="5"
                        placeholder="Product description"
                    ></textarea>

                    <label class="block my-3 text-gray-700 text-md" for="eprice">ราคา</label>
                    <input v-model="edit_price" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="text" placeholder="0.00">

                    <div class="mt-4">
                        <img v-if="edit_imgUrl" :src="edit_imgUrl" class="w-full" />
                    </div>
                    <label class="block my-3 text-gray-700 text-md" for="image">ภาพสินค้า</label>
                    <input @change="onFileChange" ref="fileupload" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="file">
                    
                    <button  @click="onUpdateProductSubmit(productId)" class="w-full px-4 py-2 mt-4 font-medium leading-5 text-white transition-colors duration-150 bg-blue-600 border border-transparent rounded-lg text-md active:bg-purple-600 hover:bg-blue-700 focus:outline-none focus:shadow-outline-purple">
                        อัพเดทข้อมูล
                    </button>
                </form>
               
            </div>
            <!-- End of Modal Content-->
        </div>
    </div>
</template>

<script>
/** axios */
import http from '@/services/ProductService.js'

export default {
    name: "EditProductModal",
    data() {
        return {
            file: null,
            edit_productId: this.productId,
            edit_name: this.name,
            edit_description: this.description,
            edit_slug: this.slug,
            edit_price: this.price,
            edit_imgUrl: this.imgUrl
        }
    },
    props:[
        'productId',
        'name',
        'description',
        'slug',
        'price',
        'imgUrl'
    ],
    methods: {
        closeEditProductModal(){
            this.$emit("closeEditProductModal");
        },

        refreshProductTable(){
            this.$emit("refreshProductTable");
        },

        onFileChange(e){
            const file = e.target.files[0]
            this.file = e.target.files[0]
            this.edit_imgUrl = URL.createObjectURL(file)
        },

        async onUpdateProductSubmit(product_id){
            // sweet alert 2 setup
            const Toast = this.$swal.mixin({
              toast: true,
              position: 'top-end',
              showConfirmButton: false,
              timer: 2500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
              }
            });

            let newProduct = new FormData()
            newProduct.append('name', this.edit_name)
            newProduct.append('slug', this.edit_slug)
            newProduct.append('description', this.edit_description)
            newProduct.append('price', this.edit_price)
            newProduct.append('file', this.file)

            /** send put method to laravel but axios not support put method */ 

            /** Sovle => send by post method and append PUT/PATCH _method in data */
            newProduct.append('_method', 'PATCH')

            http.post(`/${product_id}`,
                newProduct
            ).then( response =>{
                Toast.fire({
                icon: 'success',
                title: 'Updated product'
                }).then(()=>{
                    console.log(response);
                    this.refreshProductTable()
                    this.closeEditProductModal()
                })
            })

            
        },
    },
}
</script>