<template>
    <div class="flex items-center p-6">
      <div class="flex-1 h-full max-w-4xl mx-auto overflow-hidden bg-white rounded-lg shadow-xl dark:bg-gray-800">
        <div class="flex flex-col overflow-y-auto md:flex-row">
          <div class="h-32 md:h-auto md:w-1/2">
            <img
              aria-hidden="true"
              class="object-cover w-full h-full dark:hidden"
              src="@/assets/img/login-office.jpeg"
              alt="Office"/>
            <img
              aria-hidden="true"
              class="hidden object-cover w-full h-full dark:block"
              src="@/assets/img/login-office-dark.jpeg"
              alt="Office"/>
          </div>

          <div class="flex items-center justify-center p-6 sm:p-12 md:w-1/2">
            
            <div class="w-full">

              <h1 class="mb-4 text-xl font-semibold text-gray-700 dark:text-gray-200">เข้าสู่ระบบ</h1>

              <form @submit.prevent="onSubmit">

                <label class="block mt-3 mb-2 text-sm text-gray-700" for="email">อีเมล์</label>
                <input v-model="email" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow appearance-none" type="text" >
                <div v-if="v$.email.$error" class="mt-2 text-sm text-red-500">
                  {{ v$.email.$errors[0].$message }}
                </div>

                <label class="block mt-3 mb-2 text-sm text-gray-700" for="password">รหัสผ่าน</label>
                <input v-model="password" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow appearance-none" type="password">
                <div v-if="v$.password.$error" class="mt-2 text-sm text-red-500">
                  {{ v$.password.$errors[0].$message }}
                </div>

                <p class="my-4"></p>
                
                <!-- submit -->
                <button @click="submit()" class="block w-full px-4 py-2 mt-4 text-sm font-medium leading-5 text-center text-white transition-colors duration-150 bg-blue-600 border border-transparent rounded-lg cursor-pointer focus:outline-none focus:ring focus:ring-violet-300 active:bg-blue-600 hover:bg-blue-700">
                  เข้าสู่ระบบ
                </button>
              </form>
              
              <p class="mt-8 mb-2 text-sm">เข้าสู่ระบบผ่าน</p>  
              <button @click="submitGuest()" class="flex items-center justify-center w-full px-4 py-2 text-sm font-medium leading-5 text-white transition-colors duration-150 bg-gray-400 border border-gray-300 rounded-lg hover:bg-gray-500 dark:text-gray-400 active:bg-transparent hover:border-gray-500 focus:border-gray-500 active:text-gray-500 focus:outline-none focus:shadow-outline-gray">
                <svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5.121 17.804A13.937 13.937 0 0112 16c2.5 0 4.847.655 6.879 1.804M15 10a3 3 0 11-6 0 3 3 0 016 0zm6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                Guest
              </button>
              <button
                class="flex items-center justify-center w-full px-4 py-2 mt-4 text-sm font-medium leading-5 text-white transition-colors duration-150 bg-blue-500 border border-gray-300 rounded-lg cursor-not-allowed dark:text-gray-400 active:bg-transparent hover:border-gray-500 focus:border-gray-500 active:text-gray-500 focus:outline-none focus:shadow-outline-gray">
                <i class="mr-1 text-lg fab fa-facebook"></i>
                Login With Facebook
              </button>
              <button
                class="flex items-center justify-center w-full px-4 py-2 mt-4 text-sm font-medium leading-5 transition-colors duration-150 border border-gray-300 rounded-lg cursor-not-allowed dark:text-gray-400 active:bg-transparent hover:border-gray-500 focus:border-gray-500 active:text-gray-500 focus:outline-none focus:shadow-outline-gray">
                 <img alt="Google sign-in" class="w-5 h-5 mr-1" src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png" />
                Login With Gmail
              </button>

              <p class="mt-4">
                <router-link to="/portfolio/resetpassword" class="text-sm font-medium text-blue-600 dark:text-blue-400 hover:underline">ฉันลืมรหัสผ่าน</router-link>
              </p>
              <p class="mt-1">
                <router-link to="/portfolio/signup" class="text-sm font-medium text-blue-600 dark:text-blue-400 hover:underline">สมัครสมาชิกใหม่</router-link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, email, helpers } from '@vuelidate/validators'
import http from '@/services/AuthService'

export default {
  name: 'SignIn',
  setup () {
    return { v$: useVuelidate() }
  },
  data() {
    return {
      email: "",
      password: ""
    }
  },
  methods: {
    async submit () {
      // sweet alert 2 setup
      const Toast = this.$swal.mixin({
              toast: true,
              position: 'top-end',
              showConfirmButton: false,
              timer: 2500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
              }
      });

      // Validate (Vuelidate)
      const result = await this.v$.$validate()
      if (!result) {
        return
      }
      // call api to backend (AuthController /api/login)
      http.post('login',
        {
          "email": this.email,
          "password": this.password
        }
      ).then(response => {
          localStorage.setItem('danphranakorn_user', JSON.stringify(response.data)) //set user data to local Storage
          this.$store.commit('checkLoggedin') // call checkLoggedin to update state.isLoggedin
          Toast.fire({
              icon: 'success',
              title: 'Logging in...'
          }).then(()=>{
            this.$router.push({ name:'Dashboard'})
          })
          
      }).catch(error => {
          if(error.response){
            Toast.fire({
              icon: 'error',
              title: error.response.data.message
            })
          }
        }
      )
    },

    async submitGuest() {
      // sweet alert 2 setup
      const Toast = this.$swal.mixin({
              toast: true,
              position: 'top-end',
              showConfirmButton: false,
              timer: 2500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
              }
      });

      // call api to backend (AuthController /api/login)
      http.post('login',
        {
          "email": 'guest@gmail.com',
          "password": '1234'
        }
      ).then(response => {
          localStorage.setItem('danphranakorn_user', JSON.stringify(response.data)) //set user data to local Storage
          this.$store.commit('checkLoggedin') // call checkLoggedin to update state.isLoggedin
          Toast.fire({
              icon: 'success',
              title: 'Logging in...'
          }).then(()=>{
            this.$router.push({ name:'Services'})
          })
          
      }).catch(error => {
          if(error.response){
            Toast.fire({
              icon: 'error',
              title: error.response.data.message
            })
          }
        }
      )
    }

  },
  validations () {
    return {
      email: {  // Matches this.firstName
        required: helpers.withMessage('โปรดกรอกอีเมล', required),
        email : helpers.withMessage('รูปแบบอีเมล์ที่ป้อนไม่ถูกต้อง',email)
      },
      password: {  required: helpers.withMessage('กรอกรหัสผ่าน',required) }, // Matches this.lastName
    }
  }
}
</script>