import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './styles/app.css' // tailwind CSS
import '@fortawesome/fontawesome-free/css/all.css' // fontawesome free ( npm install --save @fortawesome/fontawesome-free ) 
// sweetalert 2 
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
// Animate on Scroll(AoS)
import AOS from 'aos'
import 'aos/dist/aos.css'

createApp(App).use(VueSweetalert2).use(store).use(router).use(AOS.init()).mount('#app')