<template>
    <div class="flex flex-wrap max-w-7xl over">
        <!-- left -->
        <div class="w-full lg:w-1/3">
            <h1 class="mb-8 text-3xl font-semibold text-center lg:mb-16">Profile Picture</h1>
            <div class="relative mx-auto mb-8 rounded-full shadow-xl w-60 h-60 ring ring-black ring-offset-base-100 ring-offset-2">
                <img class="block mx-auto rounded-full sm:mx-0 sm:shrink-0" :src="this.$store.state.avatar" alt="avatar">
                <div class="absolute right-5 bottom-5">
                    <button @click="showChangeProfilePicture = true" class="px-2 py-1 bg-white border-2 border-gray-500 rounded hover:text-white hover:bg-black">
                        <i class="far fa-edit"></i> Edit
                    </button>
                </div>
            </div>
        </div>

        <!-- right -->
        <div class="w-full px-4 mt-20 lg:w-2/3 lg:mt-0" >
            <!-- Personal Information -->
            <h1 class="mb-4 text-3xl font-semibold text-center lg:text-left">Personal Information</h1>
            <div id='section1' class="p-8 mt-6 bg-white rounded-md shadow-md lg:mt-0">
                <form @submit.prevent="onSubmit" enctype="multipart/form-data">
                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Email
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-300 rounded-md focus:bg-white" type="email" v-model="email" disabled>
                            <p class="py-2 text-sm text-gray-600">You can't change your email</p>
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Username
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="text" v-model="username">
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Firstname
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="text" v-model="fname">
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Lastname
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="text" v-model="lname">
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Phone Number
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="tel" pattern="[0-9]{9,10}" v-model="tel">
                            <p class="py-2 text-sm text-gray-600">Example: 0812345678</p>
                        </div>
                    </div>

                    <div class="md:flex md:items-center">
                        <div class="md:w-1/3"></div>
                        <div class="md:w-2/3">
                            <button @click="updatePersonalInfomaion()" type="submit" class="px-4 py-2 font-bold text-white bg-green-600 rounded shadow md:float-right hover:bg-green-500 focus:shadow-outline focus:outline-none" >
                                Save
                            </button>
                        </div>
                    </div>
                </form>

            </div>

            <!-- Change Password -->
            <h1 class="mt-20 mb-4 text-3xl font-semibold text-center lg:text-left">Change Password</h1>
            <div id='section2' :class="{'ring-2 ring-offset-4 ring-offset-gray-100 ring-red-600': changePasswordIsError}" class="p-8 mt-6 bg-white rounded-md shadow-md lg:mt-0">
                <form @submit.prevent="onSubmit" enctype="multipart/form-data">
                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Current Password
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="password" v-model="currentPassword">
                            <!-- <p class="py-2 text-sm text-gray-600">You can't change your email</p> -->
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                New Password
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="password" v-model="newPassword">
                        </div>
                    </div>

                    <div class="mb-6 md:flex">
                        <div class="md:w-1/3">
                            <label class="block pr-4 mb-3 font-bold text-gray-600 md:text-left md:mb-0" for="my-textfield">
                                Confirm New Password
                            </label>
                        </div>
                        <div class="md:w-2/3">
                            <input class="block w-full px-6 py-2 bg-gray-100 rounded-md focus:bg-white" type="password" v-model="newPassword_confirmation">
                        </div>
                    </div>

                    <div class="md:flex md:items-center">
                        <div class="md:w-1/3"></div>
                        <div class="md:w-2/3">
                            <button @click="changePassword()" type="submit" class="px-4 py-2 font-bold text-white bg-green-600 rounded shadow md:float-right hover:bg-green-500 focus:shadow-outline focus:outline-none" >
                                Change
                            </button>
                        </div>
                    </div>
                </form>

            </div>
        </div>

        <!-- Modal Chage Profile Picture -->
        <div v-if="showChangeProfilePicture" class="fixed top-0 left-0 z-20 flex items-center justify-center w-full h-full">
            <div @click="closeChageProfilePictureModal()" class="absolute w-full h-full bg-gray-900 opacity-70 "></div>
            <div class="z-50 w-11/12 p-5 mx-auto overflow-y-auto bg-white rounded shadow-lg h-4/5 modal-container md:max-w-md">
                <!-- Header -->
                <div class="flex items-center justify-center w-full h-auto">
                    <div class="flex items-start justify-start w-full h-auto py-2 text-xl text-black font-bolda">
                        Chage Profile Picture
                    </div>
                    <!-- exit modal button -->
                    <div @click="closeChageProfilePictureModal()" class="flex justify-center w-1/12 h-auto cursor-pointer">
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-x"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
                    </div>
                <!--Header End-->
                </div>
                <!-- Modal Content-->
                <div class="w-full h-auto mb-4">
                    <!-- 
                        @submit.prevent use for when click submit button page will not reload
                        enctype="multipart/form-data" use for sent files(image, gif,...)
                    -->
                    <form enctype="multipart/form-data" @submit.prevent="onSubmit"> 
                        <div class="mt-4">
                            <img :src="newAvatar" class="w-full" />
                        </div>

                        <label class="block my-3 text-gray-700 text-md" for="image">Profile picture</label>
                        <input @change="onFileChange" ref="fileUpload" class="w-full px-3 py-2 leading-tight text-gray-700 border rounded shadow" type="file">
            
                        <div class="grid grid-cols-4 gap-4">
                            <!-- Save button -->
                            <div class="col-span-2">
                                <button @click="updateProfilePicture()" type="submit" class="w-full px-4 py-2 mt-4 font-medium leading-5 text-white transition-colors duration-150 bg-blue-600 border border-transparent rounded-lg text-md active:bg-purple-600 hover:bg-blue-700 focus:outline-none focus:shadow-outline-purple">
                                    Save
                                </button>
                            </div>
                            <!-- cancle button -->
                            <div class="col-span-2">
                                <button @click="deleteProfilePicture()" class="w-full px-4 py-2 mt-4 font-medium leading-5 text-white transition-colors duration-150 bg-red-500 border border-transparent rounded-lg text-md active:bg-purple-600 hover:bg-red-700 focus:outline-none focus:shadow-outline-purple">
                                    Delete Picture
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
/** axios update profile picture */
import UpdateProfileService from '@/services/UpdateProfileService.js';

/** axios logout*/
import http from '@/services/LogoutService.js'

export default {
    name: 'ProfileManagement',
    data() {
        return {
            file: null,
            user_id: null,
            email: null,
            username: null,
            fname: null,
            lname: null,
            tel: null,
            newAvatar: this.$store.state.avatar,
            currentPassword: null,
            newPassword: null,
            newPassword_confirmation: null,
            showChangeProfilePicture: false,

            /** no important */
            changePasswordIsError: false
        }
    },
    mounted() {
        if(localStorage.getItem('danphranakorn_user')){
            this.refreshLocalStorage()
        }else{
            this.$router.push({name:'SignIn'})
      }
    },

    methods: {
        refreshLocalStorage(){
            let userStorage = localStorage.getItem('danphranakorn_user')
            let userStorageJSON = JSON.parse(userStorage);
            this.user_id = userStorageJSON['user'].id
            this.email = userStorageJSON['user'].email
            this.username = userStorageJSON['user'].username
            this.fname = userStorageJSON['user'].fname
            this.lname = userStorageJSON['user'].lname
            this.tel = userStorageJSON['user'].tel
        },

        async logout(){
            let response = await http.post('/')
            await localStorage.removeItem('danphranakorn_user')
            await this.$store.commit('checkLoggedin')
            console.log(response.data);
            this.$router.push({name: 'SignIn'})
        },

        /**
         * Update Profile Picture
         * updateProfilePicture() deleteProfilePicture() onFileChange(e) closeChageProfilePictureModal
         */
        async updateProfilePicture(){
            if(this.file){
                let newProfilePicture =  await new FormData()
                await newProfilePicture.append('file', this.file)
                await newProfilePicture.append('_method', 'PUT')

                UpdateProfileService.post(`/updateProfilePicture/${this.user_id}`,
                                            newProfilePicture
                                        ).then( response => {
                                            localStorage.setItem('danphranakorn_user', JSON.stringify(response.data))
                                            this.$store.commit('checkLoggedin')
                                            
                                            // sweet alert 2 setup
                                            const Toast = this.$swal.mixin({
                                                toast: true,
                                                position: 'top-end',
                                                showConfirmButton: false,
                                                timer: 2500,
                                                timerProgressBar: true,
                                                didOpen: (toast) => {
                                                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                                                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                                                }
                                            });

                                            Toast.fire({
                                                icon: 'success',
                                                title: 'Profile picture updated.'
                                            }).then(()=>{
                                                this.closeChageProfilePictureModal()
                                            })
                                                                                
                                        }).catch(error =>{
                                            console.log(error);
                                        })
            }else{
                console.log('No Picture file');
            }
        },

        async deleteProfilePicture(){
            let newProfilePicture =  await new FormData()
            await newProfilePicture.append('_method', 'PUT')

            UpdateProfileService.post(`/updateProfilePicture/${this.user_id}`, newProfilePicture                  
            ).then( response => {
                localStorage.setItem('danphranakorn_user', JSON.stringify(response.data))
                this.$store.commit('checkLoggedin')
                
                // sweet alert 2 setup
                const Toast = this.$swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 2500,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                    toast.addEventListener('mouseenter', this.$swal.stopTimer)
                    toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                    }
                });

                Toast.fire({
                    icon: 'success',
                    title: 'Profile picture is deleted.'
                }).then(()=>{
                    this.closeChageProfilePictureModal()
                })
                                                    
            }).catch(error =>{
                console.log(error);
            })
    
        },

        onFileChange(e){
            const file = e.target.files[0]
            this.file = e.target.files[0]
            this.newAvatar = URL.createObjectURL(file)
        },
        
        closeChageProfilePictureModal(){
            this.showChangeProfilePicture = false
            this.file = null
            this.newAvatar = this.$store.state.avatar
        },

        /**
         * Update Prersonal Infomation
         * 
         */
        async updatePersonalInfomaion(){
            /** create data for send to laravel */
            let newPersonalInfomaion =  await new FormData()
            await newPersonalInfomaion.append('fname', this.fname)
            await newPersonalInfomaion.append('lname', this.lname)
            await newPersonalInfomaion.append('username', this.username)
            await newPersonalInfomaion.append('tel', this.tel)
            await newPersonalInfomaion.append('_method', 'PUT')

            UpdateProfileService.post(`/updatePersonalInfomation/${this.user_id}`,
                                        newPersonalInfomaion
                                    ).then( response => {
                                        localStorage.setItem('danphranakorn_user', JSON.stringify(response.data))

                                        // sweet alert 2 setup
                                        const Toast = this.$swal.mixin({
                                            toast: true,
                                            position: 'top-end',
                                            showConfirmButton: false,
                                            timer: 2500,
                                            timerProgressBar: true,
                                            didOpen: (toast) => {
                                            toast.addEventListener('mouseenter', this.$swal.stopTimer)
                                            toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                                            }
                                        });

                                        Toast.fire({
                                            icon: 'success',
                                            title: 'Personal Infomation updated.'
                                        }).then(()=>{
                                            this.refreshLocalStorage()
                                        })
                                                                            
                                    }).catch(error =>{
                                        console.log(error);
                                    })

        },

        /** Change Password */
        async changePassword(){
            /** create data for send to laravel */
            let newPassword =  await new FormData()
            await newPassword.append('currentPassword', this.currentPassword)
            await newPassword.append('newPassword', this.newPassword)
            await newPassword.append('newPassword_confirmation', this.newPassword_confirmation)
            await newPassword.append('_method', 'PUT')

            // sweet alert 2 setup
            const Toast = this.$swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 2500,
                timerProgressBar: true,
                didOpen: (toast) => {
                toast.addEventListener('mouseenter', this.$swal.stopTimer)
                toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                }
            });

            UpdateProfileService.post(`/changePassword/${this.user_id}`,
                                        newPassword
                                    ).then( response => {
                                        console.log(response.data.message);

                                        Toast.fire({
                                            icon: 'success',
                                            title: 'Change password is successed.'
                                        }).then(()=>{
                                            this.logout()
                                        })
                                                                            
                                    }).catch(error =>{
                                        console.log(error);
                                        Toast.fire({
                                            icon: 'error',
                                            title: error.response.data.message
                                        }).then(()=>{
                                           this.currentPassword = null
                                           this.newPassword = null
                                           this.newPassword_confirmation = null
                                           this.changePasswordIsError = true
                                        })
                                    })

        }
    },
}
</script>