        <nav :class="{ 'navbar--hidden': hiddenNavbar, 'navbar--show': showNavbar, 'navbar--isOnTop': showNavbarIsOnTop }" class="fixed top-0 z-10 flex flex-wrap items-center justify-between w-full p-6 text-white shadow-xl bg-none" >
            <!-- Logo Title -->
            <div class="flex items-center flex-shrink-0 mr-6 text-blue-700">
                <span><i class="p-2 mr-1 -mt-1 text-xl font-bold text-white bg-blue-700 rounded-md shadow-md fab fa-dyalog"></i></span>
                <span class="text-xl font-semibold tracking-tight select-none">an Phranakorn</span>
            </div>

            <!-- (moblie size) menu button  -->
            <div class="flex items-center gap-3 md:hidden">
                <!-- menu button -->
                <button @click="showNavbarMobile = !showNavbarMobile" class="px-3 py-2 text-blue-900 border border-teal-400 rounded hover:text-white hover:border-white hover:bg-blue-900">
                    <svg class="w-3 h-3 fill-current" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z"/></svg>
                </button>

                <!-- Profile Menu-->
                <div v-if="this.$store.state.isLoggedin" class="flex w-auto shrink-0">
                    <div class="w-8">
                        <!-- avatar -->
                        <button @click="dropdownMenu = !dropdownMenu" class="align-middle rounded-full focus:shadow-outline-purple focus:outline-none shrink-0 ring-2 ring-blue-200" aria-label="Account" aria-haspopup="true">
                            <img class="object-cover w-8 h-8 rounded-full" :src="this.$store.state.avatar" alt="" aria-hidden="true" />
                        </button>

                        <!-- dropdown -->
                        <div v-if="dropdownMenu">
                            <ul class="absolute right-0 w-56 p-2 space-y-2 text-gray-500 bg-white border border-gray-100 rounded-md shadow-md mt-7 dark:border-gray-700 dark:text-gray-300 dark:bg-gray-700" aria-label="submenu"  >
                                <li class="flex">
                                    <router-link @click="dropdownMenu = false" :to="{name:'ProfileManagement'}" class="inline-flex items-center w-full px-2 py-1 text-sm font-semibold transition-colors duration-150 rounded-md hover:bg-gray-100 hover:text-gray-800 dark:hover:bg-gray-800 dark:hover:text-gray-200">
                                        <span><i class="pr-3 far fa-address-card"></i>Profile Management</span>
                                    </router-link>
                                </li>
                                <li class="flex">
                                    <router-link @click="dropdownMenu = false" :to="{name:'Services'}" class="inline-flex items-center w-full px-2 py-1 text-sm font-semibold transition-colors duration-150 rounded-md hover:bg-gray-100 hover:text-gray-800 dark:hover:bg-gray-800 dark:hover:text-gray-200" >   
                                        <span><i class="pr-3 fas fa-tasks"></i>Services</span>
                                    </router-link>
                                </li>
                                <li class="flex">
                                    <button @click="logout(); dropdownMenu = false;" class="items-center justify-between w-full px-4 py-2 text-sm font-medium text-white transition-colors duration-150 bg-red-600 border border-transparent rounded-lg active:bg-purple-600 hover:bg-red-700 focus:outline-none focus:shadow-outline-red">
                                        Logout
                                    </button>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            <!-- Desktop Menu -->
            <div class="flex-grow hidden w-full md:flex md:items-center md:w-auto md:divide-x">
                <!-- normal menu -->
                <div class="items-center w-full md:flex md:justify-end">
                    <router-link to="/" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Home</router-link>
                    <router-link to="/about" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">About</router-link>
                    <router-link to="/portfolio" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Portfolio</router-link>
                    <router-link to="/contact" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Contact</router-link>
                </div>

                <!-- Not logged in menu -->
                <div v-if="!this.$store.state.isLoggedin" class="flex md:justify-end shrink-0 w-28">
                    <router-link to="/portfolio/signup" 
                                class="inline-block px-4 py-2 mt-4 text-sm leading-none text-blue-900 border border-blue-900 rounded hover:bg-blue-900 hover:border-transparent hover:text-white md:mt-0">
                        Sign Up
                    </router-link>
                </div>
        
                <!-- Logged in menu-->
                <div v-if="this.$store.state.isLoggedin" class="flex items-center justify-end w-auto pl-4 shrink-0">
                    <!-- Profile Menu-->
                    <div class="w-8">
                        <!-- avatar -->
                        <button @click="dropdownMenu = !dropdownMenu" class="align-middle rounded-full focus:shadow-outline-purple focus:outline-none shrink-0 ring-2 ring-blue-200" aria-label="Account" aria-haspopup="true">
                            <img class="object-cover w-8 h-8 rounded-full" :src="this.$store.state.avatar" alt="" aria-hidden="true" />
                        </button>

                        <!-- dropdown -->
                        <template v-if="dropdownMenu">
                            <ul class="absolute right-0 w-56 p-2 space-y-2 text-gray-500 bg-white border border-gray-100 rounded-md shadow-md mt-7 dark:border-gray-700 dark:text-gray-300 dark:bg-gray-700" aria-label="submenu"  >
                                <li class="flex">
                                    <router-link @click="dropdownMenu = false" :to="{name:'ProfileManagement'}" class="inline-flex items-center w-full px-2 py-1 text-sm font-semibold transition-colors duration-150 rounded-md hover:bg-gray-100 hover:text-gray-800 dark:hover:bg-gray-800 dark:hover:text-gray-200">
                                        <span><i class="pr-3 far fa-address-card"></i>Profile Management</span>
                                    </router-link>
                                </li>
                                <li class="flex">
                                    <router-link @click="dropdownMenu = false" :to="{name:'Services'}" class="inline-flex items-center w-full px-2 py-1 text-sm font-semibold transition-colors duration-150 rounded-md hover:bg-gray-100 hover:text-gray-800 dark:hover:bg-gray-800 dark:hover:text-gray-200" >   
                                        <span><i class="pr-3 fas fa-tasks"></i>Services</span>
                                    </router-link>
                                </li>
                                <li class="flex">
                                    <button @click="logout(); dropdownMenu = false;" class="items-center justify-between w-full px-4 py-2 text-sm font-medium text-white transition-colors duration-150 bg-red-600 border border-transparent rounded-lg active:bg-purple-600 hover:bg-red-700 focus:outline-none focus:shadow-outline-red">
                                        Logout
                                    </button>
                                </li>
                            </ul>
                        </template>
                    </div>
                </div>
                
            </div>

            <!-- Mobile Menu -->
            <div v-if="showNavbarMobile" class="flex-grow block w-full p-4 mt-2 bg-white rounded-md md:hidden md:flex md:items-center md:w-auto md:divide-x">
                <!-- normal menu -->
                <div class="items-center w-full text-gray-400 md:flex md:justify-end">
                    <router-link @click="showNavbarMobile=false" to="/" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Home</router-link>
                    <router-link @click="showNavbarMobile=false" to="/about" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">About</router-link>
                    <router-link @click="showNavbarMobile=false" to="/portfolio" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Portfolio</router-link>
                    <router-link @click="showNavbarMobile=false" to="/contact" class="block mt-4 mr-4 md:inline-block md:mt-0 hover:text-blue-900 hover:shadow-xl">Contact</router-link>
                </div>

                <!-- Not logged in menu -->
                <div v-if="!this.$store.state.isLoggedin" class="flex md:justify-end shrink-0 w-28">
                    <router-link to="/portfolio/signup" 
                                class="inline-block px-4 py-2 mt-4 text-sm leading-none text-blue-900 border border-blue-900 rounded hover:bg-blue-900 hover:border-transparent hover:text-white md:mt-0">
                        Sign Up
                    </router-link>
                </div>
                
            </div>

            
        </nav>
    </div>

</template>

<script>
/** axios logout*/
import http from '@/services/LogoutService.js'

export default {
    name: "Nav",
    data () {
        return {
            showNavbar: true,
            hiddenNavbar: false,
            showNavbarMobile: false,
            showNavbarIsOnTop : true,
            lastScrollPosition: 0,
            dropdownMenu : false
        }
    },
    mounted () {
        this.$store.commit('checkLoggedin')
        
        window.addEventListener('scroll', this.onScroll)

        this.showNavbarIsOnTop = true
        this.showNavbar = false
        this.hiddenNavbar = false
    },
    beforeUnmount () {
        // console.log("beforemounted");
        window.removeEventListener('scroll', this.onScroll)
    },
    methods: {
        onScroll () {
            // Get the current scroll position
            const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop
            // Because of momentum scrolling on mobiles, we shouldn't continue if it is less than zero
            if (currentScrollPosition < 0) {
                return
            }
            // Here we determine whether we need to show or hide the navbar
            if(currentScrollPosition < this.lastScrollPosition){
                // when scrolling up
                this.showNavbar = true
                this.hiddenNavbar = false
            }else{
                // when scrolling down
                this.showNavbar = false
                this.hiddenNavbar = true
            }
            
            // Set the current scroll position as the last scroll position
            this.lastScrollPosition = currentScrollPosition

            // Set 'this.showNavbarIsOnTop: false' because scolling
            this.showNavbarIsOnTop = false

            if(this.lastScrollPosition == 0){
                this.showNavbarIsOnTop = true
                this.showNavbar = false
                this.hiddenNavbar = false
            }

            // console.log("last scroll position: " + this.lastScrollPosition)
        },
        async logout(){
            let response = await http.post('/')
            await localStorage.removeItem('danphranakorn_user')
            await this.$store.commit('checkLoggedin')
            console.log(response.data);
            this.$router.push({name: 'SignIn'})
        }  
    },

}
</script>

<style scoped>
    .router-link-active, .router-link-exact-active{
        @apply font-bold text-blue-600
    }
    
    .navbar--isOnTop {
        @apply shadow-none bg-none;
        transform: translate3d(0, 0, 0);
        transition: 0.25s all ease-out;
    }

    .navbar--show {
        @apply bg-white text-gray-400;
        transform: translate3d(0, 0, 0);
        transition: 0.25s all ease-out;
    }

    .navbar--hidden {
        box-shadow: none;
        transform: translate3d(0, -100%, 0);
        transition: 0.25s all ease-out;
    }
</style>