<template>
    <div>
        <div v-if="!showSearchBar" id="searchProductBar" class="fixed z-10 bg-white border-2 border-blue-900 rounded-full shadow-xl hover:text-white hover:bg-blue-900 bottom-10 right-2">
                <button @click="showSearchBar = !showSearchBar" class="px-2 py-1 text-xl"><i class="fas fa-search"></i></button>
            </div>
        <div v-if="showSearchBar" class="fixed z-10 flex items-center justify-center pl-1 bg-white rounded-md shadow-xl ring-gray-300 ring-4 bottom-10 right-2">
            <!-- search form -->
            <form @submit.prevent="onSubmit" class="flex items-center">
                <div>
                    <input class="h-8 px-3 text-sm bg-white border-2 border-gray-300 rounded-lg focus:outline-none"
                    type="search" name="search" v-model="keyword" placeholder="Search" required>
                </div>
                <!-- submit button -->
                <div>
                    <button @click="searchProductName(keyword)" class="px-2 py-1 text-xl hover:text-white hover:bg-blue-900 focus:bg-blue-900 focus:text-white"><i class="fas fa-search"></i></button>
                </div>
            </form>
            <!-- hidden search tool button -->
            <div>
                <button @click="showSearchBar = !showSearchBar" class="px-2 py-1 text-xl text-red-600 hover:bg-red-600 hover:text-white"><i class="fas fa-ban"></i></button>
            </div>
        </div>
    </div>
    
</template>

<script>
export default {
    name: 'SearchProductBar',
    data() {
        return {
            showSearchBar : true,
            keyword : ''
        }
    },
    methods: {
      searchProductName(keyword){
          this.$emit("searchProductName", keyword);
      }
    },
}
</script>