<template>
    <div>
        <!-- Table -->
        <div class="p-6 mx-auto max-w-7xl">
            <!-- Title and create button-->
            <div class="flex flex-wrap items-center justify-between mb-4">
                <!-- Title -->
                <div>
                    <h4 class="text-lg font-semibold text-gray-600 capitalize dark:text-gray-300">
                        products all ({{total}})
                    </h4>
                </div>
                
                <div class="flex gap-2">
                    <!-- refresh -->
                    <button @click="getProducts(1)" class="flex items-center justify-between px-2 text-sm text-white bg-blue-400 rounded-md hover:bg-blue-500">  
                            <svg xmlns="http://www.w3.org/2000/svg" class="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
                                <path fill-rule="evenodd" d="M4 2a1 1 0 011 1v2.101a7.002 7.002 0 0111.601 2.566 1 1 0 11-1.885.666A5.002 5.002 0 005.999 7H9a1 1 0 010 2H4a1 1 0 01-1-1V3a1 1 0 011-1zm.008 9.057a1 1 0 011.276.61A5.002 5.002 0 0014.001 13H11a1 1 0 110-2h5a1 1 0 011 1v5a1 1 0 11-2 0v-2.101a7.002 7.002 0 01-11.601-2.566 1 1 0 01.61-1.276z" clip-rule="evenodd" />
                            </svg>
                            Refresh
                    </button>
                    <!-- new product button -->
                    <button @click="showNewModal=true" class="flex items-center justify-between px-2 text-sm text-white bg-green-600 rounded-md hover:bg-green-700">  
                            <svg xmlns="http://www.w3.org/2000/svg" class="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
                                <path fill-rule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clip-rule="evenodd" />
                            </svg>
                            New
                    </button>
                </div>
            </div>

            <!-- Table -->
            <div class="w-full overflow-hidden rounded-lg shadow-xs">
                <div class="w-full overflow-x-auto">
                    <table class="w-full whitespace-no-wrap">
                        <thead>
                            <tr class="text-xs font-semibold tracking-wide text-left text-gray-500 uppercase border-b dark:border-gray-700 bg-gray-50 dark:text-gray-400 dark:bg-gray-800">
                                <th class="px-4 py-3">Number</th>
                                <th class="px-4 py-3">Product_ID</th>
                                <th class="px-4 py-3">Name</th>
                                <th class="px-4 py-3">Price</th>
                                <th class="px-4 py-3">Created_By</th>
                                <th class="px-4 py-3">Updated_By</th>
                                <th class="px-4 py-3">Actions</th>
                            </tr>
                        </thead>
                        <tbody class="bg-white divide-y dark:divide-gray-700 dark:bg-gray-800">
                            <tr v-for="(product, index) in products.data" :key="index" class="text-gray-700 dark:text-gray-400">
                                <!-- Number -->
                                <td class="px-4 py-3 text-sm">
                                    {{ (perPage * (currentPage-1)) + (index + 1) }}
                                </td>
                                <!-- Product_ID -->
                                <td class="px-4 py-3 text-xs">
                                    <span class="px-2 py-1 font-semibold leading-tight text-green-700 bg-green-100 rounded-full dark:bg-green-700 dark:text-green-100">
                                        {{product.id}}
                                    </span>
                                </td>
                                <!-- Name -->
                                <td class="px-4 py-3">
                                    <div class="flex items-center text-sm">
                                        <!-- Avatar with inset shadow -->
                                        <div class="relative flex-shrink-0 hidden w-8 h-8 mr-3 rounded-full md:block">
                                            <img
                                                class="object-cover w-full h-full rounded-full"
                                                :src="product.image"
                                                alt=""
                                                loading="lazy"
                                            />
                                            <div class="absolute inset-0 rounded-full shadow-inner" aria-hidden="true"></div>
                                        </div>
                                        <div>
                                            <!-- product.name -->
                                            <p class="font-semibold">{{product.name}}</p>
                                            <!-- product.description -->
                                            <p class="text-xs text-gray-600 dark:text-gray-400">{{product.description}}</p>
                                        </div>
                                    </div>
                                </td>
                                <!-- Price -->
                                <td class="px-4 py-3 text-xs">
                                    <span class="px-2 py-1 font-semibold leading-tight text-green-700 bg-green-100 rounded-full whitespace-nowrap dark:bg-green-700 dark:text-green-100">
                                        {{format_money(product.price)}} ฿
                                    </span>
                                </td>
                                <!-- Created by -->
                                <td class="px-4 py-3">
                                    <div class="flex items-center text-sm">
                                        <!-- Avatar with inset shadow -->
                                        <div class="relative flex-shrink-0 hidden w-8 h-8 mr-3 rounded-full md:block">
                                            <img
                                                class="object-cover w-full h-full rounded-full"
                                                :src="product.created_by_user.avatar"
                                                alt=""
                                                loading="lazy"
                                            />
                                            <div class="absolute inset-0 rounded-full shadow-inner" aria-hidden="true"></div>
                                        </div>
                                        <div>
                                            <!-- product.created_by_user -->
                                            <p class="font-semibold whitespace-nowrap">{{product.created_by_user.fname}} {{product.created_by_user.lname}}</p>
                                            <!-- product.created_at -->
                                            <p class="text-xs text-gray-600 dark:text-gray-400">{{format_date(product.created_at)}}</p>
                                        </div>
                                    </div>
                                </td>
                                <!-- Updated by -->
                                <td class="px-4 py-3">
                                    <div v-if="product.updated_by_user" class="flex items-center text-sm">
                                        <!-- Avatar with inset shadow -->
                                        <div class="relative flex-shrink-0 hidden w-8 h-8 mr-3 rounded-full md:block">
                                            <img
                                                class="object-cover w-full h-full rounded-full"
                                                :src="product.updated_by_user.avatar"
                                                alt=""
                                                loading="lazy"
                                            />
                                            <div class="absolute inset-0 rounded-full shadow-inner" aria-hidden="true"></div>
                                        </div>
                                        <div>
                                            <!-- product.updated_by_user -->
                                            <p class="font-semibold">{{product.updated_by_user.fname}}</p>
                                            <!-- product.updated_at -->
                                            <p class="text-xs text-gray-600 dark:text-gray-400">{{format_date(product.updated_at)}}</p>
                                        </div>
                                    </div>
                                    <div v-if="!product.updated_by_user">-</div>
                                </td>
                                <!-- Actions -->
                                <td class="px-4 py-3">
                                    <div class="flex items-center space-x-4 text-sm">
                                        <!-- edit svg -->
                                        <button @click="getEditProduct(product.id)" class="flex items-center justify-between px-2 py-2 text-sm font-medium leading-5 text-yellow-500 rounded-lg dark:text-gray-400 focus:outline-none focus:shadow-outline-gray" aria-label="Edit">
                                            <svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                                            </svg>
                                        </button>
                                        <!-- delete svg -->
                                        <button @click="deleteProduct(product.id)" class="flex items-center justify-between px-2 py-2 text-sm font-medium leading-5 text-red-700 rounded-lg dark:text-gray-400 focus:outline-none focus:shadow-outline-gray" aria-label="Delete">
                                            <svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                                            </svg>
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="w-full">
                        <!-- Pagination -->
                        <VueTailwindPagination
                            :current="currentPage"
                            :total="total"
                            :per-page="perPage"
                            @page-changed="onPageClick($event)"
                        />
                    </div>
                    
                </div>
            </div>
            
            
        </div>

        <!-- Search bar -->
        <SearchProductBar 
            @searchProductName="searchProductName"
        />
        

         <!-- new product modal -->
        <NewProductModal
            v-if="showNewModal"
            @closeNewProductModal="closeNewProductModal"
            @refreshProductTable="getProducts(1)"
        />

        <!-- edit product modal -->
        <EditProductModal 
            v-if="showEditModal" 
            @closeEditProductModal="closeEditProductModal"
            @refreshProductTable="refreshProductTable"
            :productId="edit_productId"
            :name="edit_name"
            :description="edit_description"
            :slug="edit_slug"
            :price="edit_price"
            :imgUrl="edit_imgUrl"
        />
        
    </div>
    
</template>

<script>
/** axios */
import http from '@/services/ProductService.js'

/** vue-tailwind-pagination */ 
import '@ocrv/vue-tailwind-pagination/dist/style.css' 
import VueTailwindPagination from '@ocrv/vue-tailwind-pagination'

/** moment (date fomat)  */
import moment from 'moment'

/** EditProductModal */
import EditProductModal from '@/components/products/EditProductModal.vue'

/** NewProductModal */
import NewProductModal from '@/components/products/NewProductModal.vue'

/** SearchProductBar */
import SearchProductBar from '@/components/products/SearchProductBar.vue'


export default {
    name: 'ProductsTable',
    components: {
        VueTailwindPagination,
        EditProductModal,
        NewProductModal,
        SearchProductBar
    },
    beforeCreate() {
        if(!localStorage.getItem('danphranakorn_user')){
            this.$router.push({ name:'Dashboard'})
        }
    },
    data() {
        return {
            showNewModal : false,
            showEditModal : false,
            products : [],
            currentPage: 1,
            perPage : 0,
            total : 0,

            /** Edit Product Modal 
             *  Props
            */
            edit_productId: 0,
            edit_name: '',
            edit_description: '',
            edit_slug: '',
            edit_price: 0,
            edit_imgUrl: '',

            keyword: null
        }
    },
    mounted() {
        this.getProducts(this.currentPage)
    },
    methods: {
        async getProducts(pageNumber){
           this.keyword = null
           let response =  await http.get(`?page=${pageNumber}`)
           this.products = response.data
           this.currentPage = response.data.current_page
           this.perPage = response.data.per_page
           this.total = response.data.total
        },

        async getProductsKeyword(pageNumber){
           let response =  await http.get(`/search/${this.keyword}?page=${pageNumber}`)
           this.products = response.data
           this.currentPage = response.data.current_page
           this.perPage = response.data.per_page
           this.total = response.data.total
        },

        async getEditProduct(product_id){
            let response =  await http.get(`/${product_id}`)
            this.edit_productId = response.data.id
            this.edit_name = response.data.name
            this.edit_description = response.data.description
            this.edit_slug = response.data.slug
            this.edit_price = response.data.price
            this.edit_imgUrl = response.data.image
            this.showEditModal = true
        },

        async deleteProduct(product_id){
            this.$swal.fire({
                title: 'Do you want to delete product id : ' + product_id,
                showCancelButton: true,
                confirmButtonText: 'delete',
            }).then((result) => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {
                    http.delete(`/${product_id}`).then( ()=>{
                        this.$swal.fire('Deleted!', 'Product id : ' + product_id, 'success')
                        if(this.keyword){
                            this.getProductsKeyword(this.currentPage)
                        }else{
                            this.getProducts(this.currentPage)
                        }
                        
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },

        format_date(value) {
            if (value) {
                return moment(String(value))
                .add(543, "year")
                .format("วันที่ DD/MM/YY เวลา HH:mm:ss");
            }
        },

        format_money(value) {
            if (value) {
                return Intl.NumberFormat("th-TH", {
                currency: "THB",
                minimumFractionDigits: 2,
                }).format(value);
            }
        },

        onPageClick(event){
            if(!this.keyword){
                this.currentPage = event
                this.getProducts(this.currentPage)
            }else{
                this.currentPage = event
                this.getProductsKeyword(this.currentPage)
            }
        },

        /** refreshProductTable emit from EditProductModal, NewProductModal*/
        refreshProductTable(){
            if(this.keyword){
                this.getProductsKeyword(this.currentPage)
            }else{
                this.getProducts(this.currentPage)
            }
        },

        /** emit from EditProductModal */
        closeEditProductModal(){
            this.showEditModal = false
        },

        /** emit from NewProductModal */
        closeNewProductModal(){
            this.showNewModal = false
        },

        /** emit from SearchProductBar */
        async searchProductName(keyword){
            if(keyword){
                let response =  await http.get(`/search/${keyword}` )
                this.products = response.data
                this.currentPage = response.data.current_page
                this.perPage = response.data.per_page
                this.total = response.data.total
                this.keyword = keyword
            }else{
                this.keyword = null
                this.getProducts(1)
            }
           
        }
    }
}
</script>