import { createRouter, createWebHistory } from 'vue-router'

// layouts
import FrontendLayout from '@/layouts/FrontendLayout.vue'
import DashboardLayout from '@/layouts/DashboardLayout.vue'
import HomeLayout from '@/layouts/HomeLayout.vue'


import Home from '@/views/home/Home.vue'
import Contact from '@/views/contact/Contact.vue'
import About from '@/views/about/About.vue'
import Portfolio from '@/views/portfolio/Portfolio.vue'
  // authentication folder
  import SignUp from '@/views/portfolio/authentication/SignUp.vue'
  import SignIn from '@/views/portfolio/authentication/SignIn.vue'
  import SignOut from '@/views/portfolio/authentication/SignOut.vue'
  import ResetPassword from '@/views/portfolio/authentication/ResetPassword.vue'
      // loggedIn folder
      import ProfileManagement from '@/views/portfolio/authentication/loggedIn/ProfileManagement.vue'
      import Services from '@/views/portfolio/authentication/loggedIn/Services.vue'
      
  // covid folder
  import CovidChart from '@/views/portfolio/covid/CovidChart.vue'

  // CRUD folder
  import Products from '@/views/portfolio/CRUD/Products.vue'


  // Notfound404 Page
import Notfound404 from '@/views/Notfound404.vue'

/** Navigation Guards */
function authGuards(to, from, next){
  if( localStorage.getItem('danphranakorn_user') ){
    next()
  }else{
    next({name: 'SignIn'})
  }
}

const routes = [
  {
    path: '/',
    component: HomeLayout,
    children: [
      {
        path: '',
        name: 'Home',
        component: Home,
        meta: {
          title: 'Home'
        }
      }
    ],

  },
  {
    path: '/about',
    component: FrontendLayout,
    children: [
      {
        path: '',
        name: 'About',
        component: About,
        meta: {
          title: 'About'
        }
      }
    ],
  },
  {
    path: '/contact',
    component: FrontendLayout,
    children: [
      {
        path: '',
        name: 'Contact',
        component: Contact,
        meta: {
          title: 'Contact'
        }
      }
    ]
  },

  /**
 * LoggedIn folder
 */

  {
    path: '/dashboard',
    component: DashboardLayout,
    beforeEnter: authGuards,
    children: [
      {
        path: '/',
        name: "Dashboard",
        component: ProfileManagement,
        meta: {
          title: 'ProfileManagement'
        }
      },
      {
        path: '/dashboard/profileManagement',
        name: "ProfileManagement",
        component: ProfileManagement,
        meta: {
          title: 'ProfileManagement'
        }
      },
      {
        path: '/dashboard/services',
        name: 'Services',
        component: Services,
        meta: {
          title: 'Services'
        }
      },
    ]
  },

  /**
   * Portfolio
   */
  {
    path: '/portfolio',
    component: FrontendLayout,
    children: [
      {
        path: '',
        name: 'Portfolio',
        component: Portfolio,
        meta: {
          title: 'Portfolio'
        }
      },

      /**
       * Authentication folder
       */
      {
        path: '/portfolio/signup',
        name: "SignUp",
        component: SignUp,
        meta: {
          title: 'Sign Up'
        }
      },
      {
        path: '/portfolio/signin',
        name: "SignIn",
        component: SignIn,
        meta: {
          title: 'Sign In'
        }
      },
      {
        path: '/portfolio/signout',
        name: "SignOut",
        component: SignOut,
        meta: {
          title: 'Sign Out'
        }
      },
      {
        path: '/portfolio/resetpassword',
        component: ResetPassword,
        name: "ResetPassword",
        meta: {
          title: 'ResetPassword'
        }
      },


      /**
       * CRUD folder
       */

      {
        path: '/portfolio/products',
        name:"Products",
        component: Products,
        meta: {
          title: 'Products'
        }
      },

      /**
       * Covid folder
       */

       {
        path: '/portfolio/covidChart',
        name: "CovidChart",
        component: CovidChart,
        meta: {
          title: 'CovidChart'
        }
      },

    ]
  },
  

  {
    path: "/:catchAll(.*)",
    component: FrontendLayout,
    children: [
      {
        path: '',
        component: Notfound404,
        meta: {
          title: '404 Not Found'
        }
      }
    ]
  }
 
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
