<template>
  <div class="p-10">
    <h1 class="text-4xl font-semibold text-center">ยอดผู้ป่วยโควิด ณ วันที่ {{format_date(covidData.txn_date)}}</h1>
      <!-- Cards -->
    <div class="grid gap-6 mt-5 mb-8 md:grid-cols-2 xl:grid-cols-4">
        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-red-500 dark:bg-gray-800 hover:text-white xl:col-span-2">
            <div class="p-3 mr-4 bg-red-100 rounded-full">
                <i class="text-4xl text-red-400 fas fa-head-side-cough"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">ผู้ป่วยใหม่</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.new_case)}}</p>
            </div>
        </div>

        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-green-500 dark:bg-gray-800 hover:text-white xl:col-span-2">
            <div class="p-3 mr-4 bg-green-100 rounded-full">
                <i class="text-4xl text-green-400 fas fa-medkit"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">รักษาหาย</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.new_recovered)}}</p>
            </div>
        </div>

        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-yellow-500 dark:bg-gray-800 hover:text-white">
            <div class="p-3 mr-4 bg-yellow-100 rounded-full">
                <i class="text-4xl text-yellow-600 fas fa-book"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">ผู้ป่วยรวม</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.total_case)}}</p>
            </div>
        </div>

        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-indigo-700 dark:bg-gray-800 hover:text-white">
            <div class="p-3 mr-4 bg-indigo-100 rounded-full">
                <i class="text-4xl text-indigo-600 fas fa-book-medical"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">รักษาหายรวม</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.total_recovered)}}</p>
            </div>
        </div>

        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-gray-500 dark:bg-gray-800 hover:text-white ">
            <div class="p-3 mr-4 bg-gray-100 rounded-full">
                <i class="text-4xl text-gray-600 fas fa-frown"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">เสียชีวิต</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.new_death)}}</p>
            </div>
        </div>

        <!-- Card -->
        <div class="flex items-center p-4 text-gray-700 bg-white rounded-lg shadow-xs hover:bg-gray-900 dark:bg-gray-800 hover:text-white">
            <div class="p-3 mr-4 bg-gray-100 rounded-full">
                <i class="text-4xl text-gray-900 fas fa-frown"></i>
            </div>
            <div>
                <p class="mb-2 text-sm font-medium dark:text-gray-400">เสียชีวิตรวม</p>
                <p class="text-lg font-semibold dark:text-gray-200">{{format_money(covidData.total_death)}}</p>
            </div>
        </div>

    </div>

    <!-- chartJS -->
    <div class="max-w-6xl mx-auto">
        <div v-if="loaded" style="height: auto;width: auto;" class="p-10">
            <vue3-chart-js
            :id="lineChart.id"
            :type="lineChart.type"
            :data="lineChart.data"
            ></vue3-chart-js>
        </div>
    </div>
    
    <!-- credit -->
    <div>
        <p class="font-thin text-center text-gray-500">ขอบคุณข้อมูลจาก: <a href="https://covid19.ddc.moph.go.th" class="underline">https://covid19.ddc.moph.go.th</a></p>
    </div>
  </div>
</template>

<script>
import http from "@/services/CovidService.js";
import moment from "moment";
import Vue3ChartJs from "@j-t-mcc/vue3-chartjs";

export default {
  name: "CovidChart",
  components: {
    Vue3ChartJs
  },
  async created(){
      await http.get("Cases/timeline-cases-all").then((result) => {
          let resultLength = result.data.length
          for (let i = 0; i  < resultLength ; i ++) {
            this.lineChart.data.labels.push(result.data[i].txn_date)
          }
          for (let i = 0; i  < resultLength ; i ++) {
            this.lineChart.data.datasets[0].data.push(result.data[i].new_case)
          }
          for (let i = 0; i  < resultLength ; i ++) {
            this.lineChart.data.datasets[1].data.push(result.data[i].new_recovered)
          }

          console.log(result);
        }).catch((err) => {
            console.log(err);
        });
      this.loaded = true
  },
  data() {
    return {
      covidData: [],
      loaded: false,
      lineChart:
      {
        id: "lineChart",
        type: "line",
        data: {
          labels: [],
          datasets: [
            {
              label: "จำนวนผู้ป่วย",
              data: [],
              fill: false,
              borderColor: "rgb(255, 51, 51)", //red
              tension: 0.1,
            },
            {
              label: "จำนวนผู้ที่ได้รับการรักษาจนหาย",
              data: [],
              fill: false,
              borderColor: "rgb(0, 153, 76)", //green
              tension: 0.1,
            },
          ],
        },
      }


    };
  },

  mounted() {
        this.getCovidData(); //call api covid this day
  },
  
  methods: {
    getCovidData() {
      http
        .get("Cases/today-cases-all")
        .then( (response) => {
            // console.log(response);
            this.covidData = response.data[0];
        })
        .catch( (error) =>{
            console.log(error);
        })
    },
    format_date(value) {
      if (value) {
        return moment(String(value))
                .add(543, "year")
                .locale('th')
                .format('LL');
      }
    },
    format_money(value) {
      if (value) {
        return Intl.NumberFormat().format(value);
      }
    }
  },
};
</script>

